import { Component, OnInit } from '@angular/core';
import { JwtService } from '../../../shared-ui/jwt.service';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
import { GlobalService } from '../../../shared-ui/global.service';
import { currentUser } from '../../../layouts/home-layout/user.model';
import { ImportStatusSearch, CompanySearch } from '../../../shared-ui/models/global.model';
import { environment } from '../../../../environments/environment';

@Component({
  selector: 'app-bulk-import-status',
  templateUrl: './bulk-import-status.component.html',
  styleUrls: ['./bulk-import-status.component.scss']
})
export class BulkImportStatusComponent implements OnInit {

  permission = {
    view: false,
    edit: false,
    delete: false
  }
  currentUser: currentUser = new currentUser;
  importStatusSearch: ImportStatusSearch = new ImportStatusSearch;
  companySearch: CompanySearch = new CompanySearch;
  bulkStatus: any;
  pagination: any;
  companies: any;
  loadingData: boolean = false;
  loadingCount: number = 1;
  order: string = 'insertTimestamp';
  reverse: boolean = true;
  
  constructor(
    private jwtService: JwtService,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private globalService: GlobalService,
  ) {
    this.globalService.sendActionChildToParent('bulkimport'); 
    this.globalService.setLoadingLabel('Loading... Please Wait.');
    this.currentUser = JSON.parse(this.jwtService.getCurrentUser());
    this.companySearch.user = this.currentUser.id;
    if(this.currentUser['isSuccessHackerAdmin']){
      this.permission = {
        view: true,
        edit: true,
        delete: true
      }
    }
  }
  
  ngOnInit() {
    this.getCompanies();
  }

  setOrder(value: string) {
    this.order = value;
    if (this.order === value) {
      this.importStatusSearch.sortOrder = this.reverse ? "DESC" : "ASC";
      this.importStatusSearch.sort = value;
      this.reverse = !this.reverse;
      this.getStatus();
    }
  }
  
  getCompanies(){
    if(!this.currentUser['isSuccessHackerAdmin']){
      this.importStatusSearch.company = this.currentUser['company'];
      this.getStatus();
      this.loadingData = true;
    }else{
      this.companies = [];
      this.spinner.show();
      this.loadingData = true;
      this.globalService.getCompanies({user: this.companySearch.user}).subscribe(data => {
        if(Object.keys(data).length){
          this.companies = data.items;
          let found = this.companies.filter(e => e.companyId == this.currentUser['company'].companyId);
          
          if(this.currentUser['company']){
            this.importStatusSearch.company = found[0];
            this.getStatus();
          }
        }
        this.spinner.hide();
      }, error => {
        this.spinner.hide();
        this.loadingData = false;
        if(error &&  error.error && error.error.message){
          this.toastr.error(error.error.message, 'Error '+error.status );
        }else{
          this.toastr.error( environment.reponseCode[error.status] , 'Error' );
        }
      });
    }
  }

  getStatus(){
    let postData = {
      company: this.importStatusSearch.company.companyId,
      user: this.currentUser.id,
      source: this.importStatusSearch.source,
      status: this.importStatusSearch.status,
      method: this.importStatusSearch.method,
      page: this.importStatusSearch.page,
      limit: this.importStatusSearch.limit,
      sort: this.importStatusSearch.sort,
      sortOrder: this.importStatusSearch.sortOrder
    }
    this.spinner.show();
    if(this.loadingCount < 2){
      this.loadingData = true;
    }
    this.globalService.getStatus(postData).subscribe(data => {
      this.loadingData = false;
      this.loadingCount++;
      if(data && Object.keys(data).length){
        this.bulkStatus = data.items;
        this.pagination = data.meta.pagination;
      }
      this.spinner.hide();
    }, error => {
      this.spinner.hide();
      this.loadingData = false;
      if(error &&  error.error && error.error.message){
        this.toastr.error(error.error.message, 'Error '+error.status );
      }else{
        this.toastr.error( environment.reponseCode[error.status] , 'Error' );
      }
    });
  }

  clearFilter(){
    this.importStatusSearch.source = "";
    this.importStatusSearch.status = "";
    this.importStatusSearch.method = "";
    this.importStatusSearch.limit = 25;
    this.importStatusSearch.sort = "insertTimestamp";
    this.importStatusSearch.sortOrder = 'ASC';
    this.reverse = true;
    this.getStatus();
  }

}
